<template>
  <q-img
    :ratio="1"
    draggable
    loading="lazy"
    fit="contain"
    spinner-color="secondary"
    :placeholder-src="file.src"
    :src="file.isSkeleton ? file.src : `${$cdn_host}/${file.path}`"
    class="rounded fit with-lightbox border-add hover-visibility no-scale"
    :alt="file.file"
    :file-id="file.id"
  >
    <template v-if="file.isSkeleton">
      <div
        class="absolute-full flex flex-center bg-accent"
        style="filter: opacity(0.5)"
      ></div>
      <div class="absolute-full flex flex-center bg-transparent">
        <div
          v-if="file.failed && file.file_info?.__status == 'failed'"
          class="q-pa-md full-width"
        >
          <div class="text-center text-negative text-body1 font-medium">
            <q-btn
              round
              outline
              icon="icon-trash-01"
              color="negative"
              @click.prevent="
                (e) => {
                  e.preventDefault();
                  $emit('delete', file);
                  e.stopPropagation();
                }
              "
            />
            <br />
            <span class="q-mt-md q-py-xs q-px-sm bg-translucent-light rounded"
              >Upload Failed</span
            >
          </div>
        </div>
        <q-linear-progress
          v-else
          indeterminate
          size="sm"
          :value="file.file_info?.__progress"
          :color="
            file.failed ||
            file.file_info?.success === false ||
            file.file_info?.__status === 'failed'
              ? 'negative'
              : file.file_info?.__progress === 1
              ? file.file_info?.__status === 'uploaded'
                ? 'secondary'
                : 'negative'
              : 'secondary'
          "
        />
      </div>
    </template>
    <q-btn
      v-if="!file.isSkeleton && avatar"
      size="sm"
      unelevated
      :icon="stared ? 'icon-star-01' : 'icon-star-01'"
      class="absolute all-pointer-events hover-text-secondary q-pa-sm ratio-1 bg-accent text-secondary"
      :class="{
        [`${stared ? 'text-secondary' : 'text-primary-500'}`]: true,
        ['hover-visibility-show']: !stared,
      }"
      style="top: 8px; left: 8px"
      @click.stop="$emit('update_avatar', file)"
    >
      <tooltip-pro>{{
        stared ? 'Unset as Brand Logo' : 'Set as Brand Logo'
      }}</tooltip-pro>
    </q-btn>
    <div
      v-if="!noTitle"
      class="absolute-bottom text-subtitle1 text-center hover-visibility-show"
    >
      {{ file.file }}
    </div>
    <q-icon
      v-if="
        (editing && !file.isSkeleton) || file?.file_info?.__status == 'failed'
      "
      flat
      round
      :size="optionSize"
      name="icon-dots-horizontal"
      class="absolute-top-right all-pointer-events text-primary-500"
      style="top: 8px; right: 8px"
      @click.stop
    >
      <q-menu auto-close>
        <q-list separator>
          <q-item
            v-if="!file.isSkeleton"
            clickable
            class="hover-text-secondary"
            @click="$downloadItem(`${$cdn_host}/${file.path}`, file.file)"
          >
            <q-item-section side class="q-pr-sm">
              <q-icon name="icon-download-02" size="xs" />
            </q-item-section>
            <q-item-section>Download</q-item-section>
          </q-item>
          <q-item
            v-if="!file.isSkeleton"
            clickable
            class="hover-text-secondary"
            @click="openRenameDialog = true"
          >
            <q-item-section side class="q-pr-sm">
              <q-icon name="icon-edit-03" size="xs" />
            </q-item-section>
            <q-item-section>Rename</q-item-section>
          </q-item>
          <q-item
            clickable
            class="text-negative hover-text-negative"
            @click="
              (removingFile?.id && file.id === removingFile?.id) ||
              (removingFile?.__key &&
                file.file_info?.__key === removingFile?.__key)
                ? void 0
                : $emit('delete', file)
            "
          >
            <q-item-section side class="q-pr-sm">
              <q-icon name="icon-trash-01" size="xs" color="negative" />
            </q-item-section>
            <q-item-section>Delete</q-item-section>
          </q-item>
        </q-list>
      </q-menu>
    </q-icon>

    <q-dialog v-model="openRenameDialog">
      <RenameFile
        :file="file"
        @close="openRenameDialog = false"
        @success="renameUpdate"
      />
    </q-dialog>
  </q-img>
</template>

<script>
import { defineComponent } from 'vue';
import RenameFile from 'src/components/Dialogs/RenameFile.vue';

export default defineComponent({
  name: 'ImageLoader',

  emits: ['renamed', 'delete', 'update_avatar'],

  components: { RenameFile },

  data() {
    return {
      openRenameDialog: false,
    };
  },

  props: {
    file: {
      type: Object,
      default: () => {},
    },
    removingFile: {
      type: Object,
      default: () => {},
    },
    editing: {
      type: Boolean,
      default: false,
    },
    stared: {
      type: Boolean,
      default: false,
    },
    avatar: {
      type: Boolean,
      default: false,
    },
    noTitle: {
      type: Boolean,
      default: false,
    },
    optionSize: {
      type: String,
      default: 'sm',
    },
  },

  methods: {
    renameUpdate(fileInfo) {
      this.$emit('renamed', fileInfo);
    },
  },
});
</script>
